import React from 'react';
import styled from 'styled-components';
import { HTML5Backend } from 'react-dnd-html5-backend';
import TouchBackend from 'react-dnd-touch-backend';
import {
  DndProvider,
  TouchTransition,
  MouseTransition,
} from 'react-dnd-multi-backend';
import { GroupType } from 'App/context';
import Basket from './Basket';
import CenterBasket from './CenterBasket';

export const HTML5toTouch = {
  backends: [
    {
      id: 'html5',
      backend: HTML5Backend,
      transition: MouseTransition,
    },
    {
      id: 'touch',
      backend: TouchBackend,
      options: { enableMouseEvents: true },
      preview: true,
      transition: TouchTransition,
    },
  ],
};

const NameDiv = styled.div`
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50vw;
  height: 12vh;
  font-weight: 800;
  z-index: -1;
  font-size: 3rem;
  color: #e0e0e0;
  bottom: 0;
`;

const DndBoard: React.FC = () => (
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  <DndProvider options={HTML5toTouch as any}>
    <CenterBasket />
    <div
      style={{
        position: 'absolute',
        top: 0,
        left: 0,
      }}
    >
      <Basket groupType={GroupType.Group1} />
      <NameDiv>그룹1</NameDiv>
    </div>
    <div
      style={{
        position: 'absolute',
        top: 0,
        right: 0,
      }}
    >
      <Basket groupType={GroupType.Group2} />
      <NameDiv>그룹2</NameDiv>
    </div>
    <div
      style={{
        position: 'absolute',
        bottom: 0,
        left: 0,
      }}
    >
      <Basket groupType={GroupType.Group3} />
      <NameDiv>그룹3</NameDiv>
    </div>
    <div
      style={{
        position: 'absolute',
        bottom: 0,
        right: 0,
      }}
    >
      <Basket groupType={GroupType.Group4} />
      <NameDiv>그룹4</NameDiv>
    </div>
  </DndProvider>
);

export default DndBoard;
