import React from 'react';
import ButtonGroup from 'components/ButtonGroup';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import styled from 'styled-components';
import ReadOnlyBasket from 'components/ReadOnlyBasket';
import { polygonsGroupByCluster } from 'App/datas';

const StyledContent = styled(Content)`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 80%;
  height: fit-content;
  margin: auto;
  margin-top: 4rem;
`;

const pointViews = [{ title: '크기' }, { title: '색깔' }, { title: '모양' }];

const AnalyticsProgress: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>종합 분석 과정</ContentTitle>
          <ExplainBox>
            종합 정리된 그룹을 예시로
            <br />
            어떻게 세 가지 관점에서 동시에 고려하는지 알아보자!
          </ExplainBox>
          <StyledContent>
            {pointViews.map((pointView) => (
              <div key={pointView.title}>
                <h2>{pointView.title}</h2>
                <p style={{ fontSize: '1rem', fontWeight: 'bold' }}>
                  Q. 같은 {pointView.title}의 도형은 최대 몇 개일까?
                </p>
                <ReadOnlyBasket group={polygonsGroupByCluster[2]} />
              </div>
            ))}
          </StyledContent>
        </DivBase>
        <ButtonGroup buttonText="정답 확인" />
      </Container>
    </BasePage>
  );
};

export default AnalyticsProgress;
